import React from 'react';
import { BrowserRouter, Route, Switch } from "react-router-dom";
import NavTop from './components/navtop'
import Education from './components/eductaions'
import Framework from './components/framework'
import Introduction from './components/Introduction'
import Programming_languague from './components/programming_languague'
import Project from './components/projects'
import Project_details from "./components/pro_details";
import Experience from "./components/experience";

function App() {
  return (
    <BrowserRouter>
      <div className="container">
        <NavTop />
        <Switch>
          <Route path="/" exact component={Introduction} />
          <Route path="/edu" exact component={Education} />
          <Route path="/frmwrk" exact component={Framework} />
          <Route path="/pro_lan" exact component={Programming_languague} />
          <Route path="/proj" exact component={Project} />
          <Route path="/exp" exact component={Experience} />
          <Route path="/pro/details/:pd_id" exact component={Project_details} />
        </Switch>
      </div>
    </BrowserRouter>
  );
}

export default App;
